.work {
    background-color:  rgba(128, 128, 128, 0.101);
    margin-left: 140px;
  }

  .work-exp {
    padding: 50px 0px;
  }
  
  .work-exp h2 {
    font-weight: bold;
    color: gray
  }
  
  .work-exp hr {
    color: #138781;
    width: 400px;
    margin: 10px auto;
  }
  
  /* ===== DARK MODE ========== */
#dark .work {
  background-color: #643a6b;
}

#dark .work-exp h2 {
  color: white;
}


@media (max-width: 600px) {
  .work {
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }
  .work hr {
    width: 280px;
  }
}