.home-container{
    height: 300px;
    color: white;
    margin-left: 100px;
}

.home-content h2{
    padding: 5px;
    padding-top: 80px;
   /*margin-left: 100px; */
}
.home-content h1{
   color: rebeccapurple;
   padding: 5px;
   /*margin-left: 100px; */
}

.home-buttons{
     /*margin-left: 100px; */
     margin-top: 20px;
}
.home-buttons .btn-hire{
   background-color: #138781; 
   color: white;
   width: 150px;
}
.home-buttons .btn-hire:hover{
    background-color: rgb(247, 247, 247); 
    color:  rgb(61, 108, 201); ;
    width: 190px;
    transition: 1s;
 }
 .home-buttons .btn-cv{
    background-color: rgb(239, 194, 111);
    color: white;
    margin-left: 15px;
 }
 .home-buttons .btn-cv:hover{
    opacity: 80%;
    transition: 1s;
 }
 
.theme-btn {
   display: flex;
   align-items: center;
   justify-content: center;
   height: 40px;
   width: 40px;
   background-color: #1e1e1e;
   margin-top: 20px;
   text-align: center;
   position: fixed;
   right: 0;
   cursor: pointer;
   border-top-left-radius: 10px;
   border-bottom-left-radius: 10px;
   z-index: 1;
 }
 

 /* =============== */
@media (max-width: 600px) {
   .home-container {
     padding: 0;
     margin: 0;
     margin-top: 50px;
   }
   .home-container h2 {
     font-size: 24px;
   }
   .home-container h1 {
     font-size: 24px;
   }
 }